$(document).ready(function() {
    var fullName = $('#InputShippingFullName').val();
    var street = $('#InputShippingAddress').val();
    var postalCode = $('#InputShippingPostalCode').val();
    var city = $('#InputShippingCity').val();
    var country = $('#InputShippingCountry').val();

    $('#autofillShipping').on('ifChecked', function() {
        $('#InputShippingFullName').val($('#InputBillingFullName').val()); 
        $('#InputShippingAddress').val($('#InputBillingAddress').val()); 
        $('#InputShippingPostalCode').val($('#InputBillingPostalCode').val()); 
        $('#InputShippingCity').val($('#InputBillingCity').val()); 
        $('#InputShippingCountry').val($('#InputBillingCountry').val()); 
    });

    $('#autofillShipping').on('ifUnchecked', function() {
        $('#InputShippingFullName').val(fullName); 
        $('#InputShippingAddress').val(street); 
        $('#InputShippingPostalCode').val(postalCode);
        $('#InputShippingCity').val(city);
        $('#InputShippingCountry').val(country);
    });
});